'use client';
import { useState, useEffect } from 'react';
import { initializeApp } from "firebase/app";
import { 
  getFirestore, 
  collection, 
  addDoc, 
  getDocs, 
  doc, 
  deleteDoc, 
  updateDoc,
  serverTimestamp 
} from "firebase/firestore";


// Initialize Firebase
const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

export default function AdminPage() {
  const [songs, setSongs] = useState([]);
  const [form, setForm] = useState({ title: '', artist: '', genre: '' });
  const [editId, setEditId] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á‡∏à‡∏≤‡∏Å Firebase ‡πÄ‡∏°‡∏∑‡πà‡∏≠‡∏Ñ‡∏≠‡∏°‡πÇ‡∏û‡πÄ‡∏ô‡∏ô‡∏ï‡πå‡∏ñ‡∏π‡∏Å‡πÇ‡∏´‡∏•‡∏î
  useEffect(() => {
    fetchSongs();
  }, []);

  // ‡∏ü‡∏±‡∏á‡∏Å‡πå‡∏ä‡∏±‡∏ô‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á‡∏ó‡∏±‡πâ‡∏á‡∏´‡∏°‡∏î‡∏à‡∏≤‡∏Å Firebase
  const fetchSongs = async () => {
    try {
      setLoading(true);
      const songsCollection = collection(db, "songs");
      const songsSnapshot = await getDocs(songsCollection);
      const songsList = songsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setSongs(songsList);
      setError(null);
    } catch (err) {
      console.error("Error fetching songs:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡πÇ‡∏´‡∏•‡∏î‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  // ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á‡πÉ‡∏´‡∏°‡πà‡∏•‡∏á‡πÉ‡∏ô Firebase
  const handleAdd = async () => {
    if (!form.title || !form.artist) return;
    
    try {
      setLoading(true);
      // ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏•‡∏á‡πÉ‡∏ô Firebase
      const docRef = await addDoc(collection(db, "songs"), {
        title: form.title,
        artist: form.artist,
        genre: form.genre || "",
        createdAt: serverTimestamp()
      });
      
      // ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏ô state
      setSongs([
        ...songs,
        { 
          id: docRef.id, 
          title: form.title, 
          artist: form.artist, 
          genre: form.genre || "" 
        },
      ]);
      
      // ‡∏£‡∏µ‡πÄ‡∏ã‡πá‡∏ï‡∏ü‡∏≠‡∏£‡πå‡∏°
      setForm({ title: '', artist: '', genre: '' });
      setError(null);
    } catch (err) {
      console.error("Error adding song:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  // ‡∏•‡∏ö‡πÄ‡∏û‡∏•‡∏á‡∏≠‡∏≠‡∏Å‡∏à‡∏≤‡∏Å Firebase
  const handleDelete = async (id) => {
    try {
      setLoading(true);
      // ‡∏•‡∏ö‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏à‡∏≤‡∏Å Firebase
      await deleteDoc(doc(db, "songs", id));
      
      // ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏ô state
      setSongs(songs.filter((song) => song.id !== id));
      setError(null);
    } catch (err) {
      console.error("Error deleting song:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡∏•‡∏ö‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  const handleEdit = (song) => {
    setEditId(song.id);
    setForm({ 
      title: song.title, 
      artist: song.artist, 
      genre: song.genre || "" 
    });
  };

  // ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÄ‡∏û‡∏•‡∏á‡πÉ‡∏ô Firebase
  const handleUpdate = async () => {
    if (!form.title || !form.artist) return;
    
    try {
      setLoading(true);
      // ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏ô Firebase
      const songRef = doc(db, "songs", editId);
      await updateDoc(songRef, {
        title: form.title,
        artist: form.artist,
        genre: form.genre || "",
        updatedAt: serverTimestamp()
      });
      
      // ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏ô state
      setSongs(
        songs.map((song) =>
          song.id === editId ? { 
            ...song, 
            title: form.title, 
            artist: form.artist, 
            genre: form.genre || "" 
          } : song
        )
      );
      
      // ‡∏£‡∏µ‡πÄ‡∏ã‡πá‡∏ï‡∏™‡∏ñ‡∏≤‡∏ô‡∏∞‡∏Å‡∏≤‡∏£‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç
      setEditId(null);
      setForm({ title: '', artist: '', genre: '' });
      setError(null);
    } catch (err) {
      console.error("Error updating song:", err);
      setError("‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ï‡πÄ‡∏û‡∏•‡∏á‡πÑ‡∏î‡πâ");
    } finally {
      setLoading(false);
    }
  };

  // ‡∏¢‡∏Å‡πÄ‡∏•‡∏¥‡∏Å‡∏Å‡∏≤‡∏£‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç
  const handleCancelEdit = () => {
    setEditId(null);
    setForm({ title: '', artist: '', genre: '' });
  };

  return (
    <div className="max-w-3xl mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">üéß ‡∏à‡∏±‡∏î‡∏Å‡∏≤‡∏£‡πÄ‡∏û‡∏•‡∏á (Admin)</h1>
      
      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          <p>{error}</p>
        </div>
      )}

      <div className="bg-white rounded-xl shadow p-4 mb-6">
        <h2 className="text-xl font-semibold mb-2">{editId ? '‚úèÔ∏è ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç‡πÄ‡∏û‡∏•‡∏á' : '‚ûï ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á'}</h2>
        <input
          type="text"
          name="title"
          placeholder="‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏û‡∏•‡∏á"
          value={form.title}
          onChange={handleChange}
          className="border p-2 mr-2 mb-2 w-full"
        />
        <input
          type="text"
          name="artist"
          placeholder="‡∏ä‡∏∑‡πà‡∏≠‡∏®‡∏¥‡∏•‡∏õ‡∏¥‡∏ô"
          value={form.artist}
          onChange={handleChange}
          className="border p-2 mr-2 mb-2 w-full"
        />
        <input
          type="text"
          name="genre"
          placeholder="‡πÅ‡∏ô‡∏ß‡πÄ‡∏û‡∏•‡∏á (Pop, Rock...)"
          value={form.genre}
          onChange={handleChange}
          className="border p-2 mr-2 mb-2 w-full"
        />
        <div className="flex space-x-2">
          {editId ? (
            <>
              <button 
                onClick={handleUpdate} 
                className="bg-yellow-500 text-white px-4 py-2 rounded"
                disabled={loading}
              >
                {loading ? '‡∏Å‡∏≥‡∏•‡∏±‡∏á‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï...' : '‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï'}
              </button>
              <button 
                onClick={handleCancelEdit} 
                className="bg-gray-500 text-white px-4 py-2 rounded"
                disabled={loading}
              >
                ‡∏¢‡∏Å‡πÄ‡∏•‡∏¥‡∏Å
              </button>
            </>
          ) : (
            <button 
              onClick={handleAdd} 
              className="bg-blue-500 text-white px-4 py-2 rounded"
              disabled={loading}
            >
              {loading ? '‡∏Å‡∏≥‡∏•‡∏±‡∏á‡πÄ‡∏û‡∏¥‡πà‡∏°...' : '‡πÄ‡∏û‡∏¥‡πà‡∏°'}
            </button>
          )}
        </div>
      </div>

      <div className="bg-white rounded-xl shadow p-4">
        <h2 className="text-xl font-semibold mb-4">üìÑ ‡∏£‡∏≤‡∏¢‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏û‡∏•‡∏á</h2>
        
        {loading && songs.length === 0 ? (
          <p className="text-gray-500">‡∏Å‡∏≥‡∏•‡∏±‡∏á‡πÇ‡∏´‡∏•‡∏î‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•...</p>
        ) : songs.length === 0 ? (
          <p className="text-gray-500">‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡πÄ‡∏û‡∏•‡∏á</p>
        ) : (
          <ul className="divide-y">
            {songs.map((song) => (
              <li key={song.id} className="flex justify-between items-center py-3">
                <div>
                  <p className="font-semibold">{song.title}</p>
                  <p className="text-sm text-gray-600">
                    {song.artist} {song.genre && `‚Ä¢ ${song.genre}`}
                  </p>
                </div>
                <div className="flex space-x-3">
                  <button
                    onClick={() => handleEdit(song)}
                    className="text-yellow-500 hover:underline"
                    disabled={loading}
                  >
                    ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç
                  </button>
                  <button
                    onClick={() => handleDelete(song.id)}
                    className="text-red-500 hover:underline"
                    disabled={loading}
                  >
                    ‡∏•‡∏ö
                  </button>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
}